import React from "react";

function SharedLayout({ children, guestName }) {
  return (
    <div
      className="shared-layout"
      style={{
        position: "relative",
        backgroundImage: `url(${process.env.PUBLIC_URL}/photos/photo${guestName.photo}.jpg)`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        minHeight: "100vh",
        display: "flex", // flexbox 
        justifyContent: "center", //   
        alignItems: "center", //   
        color: "black",
      }}
    >
      {/*    */}
      <div
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          width: "100%",
          height: "100%",
          backgroundColor: "rgba(255, 255, 255, 0.5)",
          zIndex: 1,
        }}
      ></div>

      {/*   */}
      <div
        style={{
          position: "relative",
          zIndex: 2,
          textAlign: "center", //   
        }}
        className="container py-5"
      >
        {children}
      </div>
    </div>
  );
}

export default SharedLayout;