import React from "react";
import { useNavigate } from "react-router-dom";
import SharedLayout from "./SharedLayout";

function InvitationPage({ guestName }) {
  const navigate = useNavigate();

  return (
    <SharedLayout guestName={guestName}>
      <h1 className="mb-4" style={{ color: "#fc7fbe", fontWeight: "bold" }}>Happy Birthday </h1>
      <p className="fs-4" style={{ fontWeight: "bold" }}>{guestName.name} !</p>
      <p className="fs-3" style={{ fontWeight: "bold" }}>: 2025 1 18 () 19 30~</p>
      <p className="fs-3" style={{ fontWeight: "bold" }}>: </p>

      <div className="d-flex justify-content-center mt-4">
    {/* 1  */}
    <div className="me-5">
      <h3 className="fs-3" style={{ color: "#fc7fbe", fontWeight: "bold" }}>1</h3>
      <ul className="list-unstyled">
        <li style={{ fontSize: "20px" , fontWeight: "bold"}}>19 30 </li>
        <li style={{ fontSize: "20px" , fontWeight: "bold"}}>19 40 </li>
        <li style={{ fontSize: "20px" , fontWeight: "bold"}}>20 30   </li>
        <li style={{ fontSize: "20px" , fontWeight: "bold"}}>21 10 </li>
      </ul>
    </div>

    {/* 2  */}
    <div>
      <h3 className="fs-3" style={{ color: "#fc7fbe", fontWeight: "bold" }}>2</h3>
      <ul className="list-unstyled">
        <li style={{ fontSize: "20px" , fontWeight: "bold"}}>21 30 </li>
        <li style={{ fontSize: "20px" , fontWeight: "bold"}}>21 40 </li>
        <li style={{ fontSize: "20px" , fontWeight: "bold"}}>22 00 </li>
      </ul>
    </div>
  </div>

      <button
        className="btn btn-pink mt-5"
        onClick={() => navigate("/lucky-draw")}
      >
         
      </button>
    </SharedLayout>
  );
}

export default InvitationPage;