import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import EnvelopePage from './EnvelopePage';
import PhotoPage from './PhotoPage';
import InvitationPage from './InvitationPage';
import LuckyDrawPage from './LuckyDrawPage';
import OrderFoodPage from './OrderFoodPage';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import './btn-pink.css';



function App() { 


  const [guestName, setGuestName] = useState(''); //  


  return (
    <Router basename={process.env.PUBLIC_URL}> {/* GitHub Pages basename .  */}
      <Routes>
        <Route path="/" element={<EnvelopePage />} /> {/*   */}
        <Route path="/photo" element={<PhotoPage setGuestName={setGuestName} />} />
        <Route path="/invitation" element={<InvitationPage guestName={guestName} />} />
        <Route path="/lucky-draw" element={<LuckyDrawPage guestName={guestName} />} />
        <Route path="/order-food" element={<OrderFoodPage guestName={guestName} />} />
      </Routes>
    </Router>
  );
}


export default App;