import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import "bootstrap/dist/css/bootstrap.min.css";
import "./PhotoPage.css"; //  CSS 

function PhotoPage({ setGuestName }) {

  const [showHeader, setShowHeader] = useState(true); //   
  const [showPhotos, setShowPhotos] = useState(true); //   
  const navigate = useNavigate();


  const handlePhotoClick = (photoIndex) => {
    Swal.fire({
      title: "  ",
      input: "text",
      inputPlaceholder: " ",
      showCancelButton: true,
      confirmButtonText: "",
      cancelButtonText: "",
      preConfirm: (value) => {
        const regex = /^[a-zA-Z-0-9]{1,20}$/; //  ,  20 
        if (!regex.test(value)) {
          Swal.showValidationMessage(
            " , ,   20  ."
          );
          return false;
        }
        return value;
      },
    }).then((result) => {
      if (result.isConfirmed && result.value) {
        const guestName = result.value;
        setGuestName({ name: guestName, photo: photoIndex });
        navigate("/invitation");
      }
    });
  };

  return (
    <div
    className="photo-page-container"
    style={{
      backgroundColor: "rgb(255 230 240 / 30%)", //   
      minHeight: "100vh",
      display: "flex",
      flexDirection: "column", //  
      justifyContent: "center",
      alignItems: "center",
    }}
  >
    {showHeader && (
      <h1
        className={`mb-4 fade-in`}
        style={{
          color: "black", // 
          padding: "1rem", //   
          marginBottom: "2rem", //    
        }}
      >
            
      </h1>
    )}

{showPhotos && (
  <div className="row g-3 justify-content-center photo-grid">
    {[1, 2, 3, 4, 5, 6].map((_, index) => (
      <div
        key={index}
        className="col-6 col-md-4 photo-slide-in"
        onClick={() => handlePhotoClick(index + 1)}
        style={{ cursor: "pointer" }}
      >
        <img
          src={`${process.env.PUBLIC_URL}/photos/photo${index + 1}.jpg`}
          alt={`photo-${index + 1}`}
          className="img-fluid rounded shadow"
          style={{
            width:
              index === 0
                ? "150px" // photo 1
                : index === 1
                ? "250px" // photo 2
                : index === 2
                ? "150px" // photo 3
                : index === 3
                ? "250px" // photo 4
                : index === 4
                ? "150px" // photo 5
                : "200px", // photo 6
            height: "auto", //   
            transform: `rotate(${index % 2 === 0 ? -10 : 10}deg)`,
          }}
        />
      </div>
    ))}
  </div>
)}
    </div>
  );
}

export default PhotoPage;