import React from "react";
import { useNavigate } from "react-router-dom";

function EnvelopePage() {
  const navigate = useNavigate();

  const handleEnvelopeClick = () => {
    navigate("/photo"); //    
  };

  return (

    
    <div
      style={{
        backgroundColor: "rgb(255 230 240 / 30%)", //   
        minHeight: "100vh",
        display: "flex",
        flexDirection: "column", //  
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      {/*   */}
      <h1
        className={`mb-4 fade-in`}
        style={{
          color: "black", // 
          padding: "1rem", //   
          marginBottom: "2rem", //    
        }}
      >
          
      </h1>

      {/*   */}
      <img
        src={`${process.env.PUBLIC_URL}/photos/gift-bow.png`}
        alt="envelope"
        style={{
          cursor: "pointer",
          width: "200px", //   
          height: "auto", //  
        }}
        onClick={handleEnvelopeClick}
      />
    </div>
  );
}

export default EnvelopePage;